@keyframes scaleUp{0%{transform:scale(0)}50%{transform:scale(1.15)}100%{transform:scale(1)}}.wrap{padding:50px;min-height:100vh;box-sizing:border-box;background:linear-gradient(to right bottom, rgb(255, 251, 235), rgb(255, 237, 213))}.wrap .header{margin-bottom:30px;text-align:center;color:#8b5a2b;font-size:55px;font-weight:bold}.wrap .infoBar{padding:0 20px;display:flex;justify-content:space-between;align-items:center;height:100px;border-radius:10px;box-sizing:border-box;box-shadow:0 5px 10px rgba(0,0,0,.3);background:#fffdf8}.wrap .infoBar .currentPlayer{display:flex;justify-content:center;align-items:center;font-size:26px}.wrap .infoBar .currentPlayer .stone{width:60px;height:60px;animation:scaleUp .3s ease-out}.wrap .infoBar .currentPlayer .stone>img{width:100%;height:100%;object-fit:contain;border-radius:50%}.wrap .infoBar .currentPlayer .stone>img.black{box-shadow:0 0 10px #ff8818}.wrap .infoBar .currentPlayer .stone>img.white{box-shadow:0 0 10px #fff}.wrap .infoBar .currentPlayer .text{padding-left:15px}.wrap .infoBar .currentPlayer .player{color:#8b5a2b}.wrap .infoBar .btns{display:flex;justify-content:center;align-items:center;gap:20px}.wrap .infoBar .btns .skinBtn,.wrap .infoBar .btns .restartBtn{display:flex;justify-content:center;align-items:center;width:100px;height:60px;border-radius:12px;background:#8b5a2b;color:#fff;font-size:24px}.wrap .main{padding:60px 0}.wrap .main .board{margin:0 auto;width:615px;height:615px;background:#deb887}.wrap .main .board .boardRow{display:flex;justify-content:center}.wrap .main .board .boardRow .boardCell{display:flex;justify-content:center;align-items:center;width:40px;height:40px;border-top:1px solid rgba(139,90,43,.6);border-left:1px solid rgba(139,90,43,.6);font-size:20px}.wrap .main .board .boardRow .boardCell .stone{width:38px;height:38px;animation:scaleUp .3s ease-out}.wrap .main .board .boardRow .boardCell .stone>img{width:100%;height:100%;object-fit:contain;border-radius:50%}.wrap .main .board .boardRow .boardCell .stone>img.black{box-shadow:0 0 10px #ff8818}.wrap .main .board .boardRow .boardCell .stone>img.white{box-shadow:0 0 10px #fff}.wrap .main .board .boardRow .boardCell:last-child{border-right:1px solid rgba(139,90,43,.6)}.wrap .main .board .boardRow:last-child .boardCell{border-bottom:1px solid rgba(139,90,43,.6)}.wrap .rule{padding:20px;border-radius:10px;box-sizing:border-box;box-shadow:0 5px 10px rgba(0,0,0,.3);background:#fffdf8}.wrap .rule h3{color:#8b5a2b;font-size:28px}.wrap .rule ul{margin-top:10px}.wrap .rule ul li{line-height:40px;color:#374151;font-size:25px}.modal{position:absolute;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,.5)}.modal .inner{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);padding:30px;width:80%;height:80%;border-radius:20px;box-sizing:border-box;background:#fff}.modal .inner .cls{position:absolute;top:20px;right:20px;width:35px;height:35px;background:url("./assets/cls.png");background-size:100% 100%}.modal .inner .title{text-align:center;color:#8b5a2b;font-size:38px;font-weight:bold}.modal .inner .content{padding-top:20px}.modal .inner .content .item{margin-bottom:30px}.modal .inner .content .item .label{padding:10px 0;text-align:center;font-size:30px}.modal .inner .content .item ul{display:flex;flex-wrap:wrap}.modal .inner .content .item ul li{position:relative;margin-bottom:20px;display:flex;justify-content:center;width:25%;height:80px}.modal .inner .content .item ul li>img{width:auto;height:100%}.modal .inner .content .item ul li.on::after{position:absolute;bottom:5px;right:5px;content:"";width:30px;height:30px;background:url("./assets/selected.png");background-size:100% 100%}